@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400;700&display=swap);
*{box-sizing:border-box}.card{margin:1px 3px;display:inline-block;height:150px;width:150px;overflow:hidden;position:relative;background-color:white}.card--m{height:120px;width:120px}.card--l{height:90px;width:90px}.button{background-color:transparent;border:none;position:absolute;top:0;left:0;height:100%;width:100%;margin:0;padding:0;z-index:5;color:transparent}.button:hover{cursor:pointer}.button:focus{outline:none}.card__image{position:absolute;bottom:0;left:0;width:100%;background-color:white}.card__image-wrapper{position:relative;padding-top:100%}

.buttons{padding:10px 20px;margin:10px 5px;border:none;font-family:monospace;text-transform:uppercase;background-color:#f7eaa4}.buttons:hover{background-color:#ffe865;cursor:pointer}.buttons:last-child{background-color:#ffe865}.buttons:last-child:hover{background-color:#f7eaa4;cursor:pointer}

body{
    font-family: 'Josefin Sans', sans-serif;
}

.app {
    margin-top: 20px;
}

.flex {
    display:flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.layout {
    margin-top: 40px;
    flex-basis: auto !important;
}

.layout--l {
    max-width: 1000px;
    width: 1000px;
    margin-top: 20px;
}

.layout--m {
    max-width: 800px;
    width: 800px;
}

.layout--s {
    max-width: 700px;
    width: 700px;
    margin-top: 40px;
}
